<template>
  <v-card>
    <v-card-text>
      <v-row>
        <v-col cols="6">
          <!-- <v-text-field v-model="list.staff_property_group_name"
                      :error-messages="$errors('list','staff_property_group_name',requiredRule)"
                      :readonly='readonly'
                      append-icon="mdi-longitude"
                      label="Staff Property Group Name*"></v-text-field> -->
          <mrc_ess_multiText v-model="list.staff_property_group_name"
                             :isNational="judegNational('staff_property_group_name')"
                             :error-messages="$errors('list','staff_property_group_name',requiredRule)"
                             :readonly='readonly'
                             label="Staff Property Group Name*"></mrc_ess_multiText>
        </v-col>
        <v-col cols="6">
          <!-- <v-text-field v-model="list.staff_property_group_code"
                      :readonly='readonly'
                      label="Staff Property Group Code*"></v-text-field> -->
          <mrc_ess_multiText v-model="list.staff_property_group_code"
                             :isNational="judegNational('staff_property_group_code')"
                             :readonly='readonly'
                             label="Staff Property Group Code*"></mrc_ess_multiText>
        </v-col>
        <v-col cols="12">
          <!-- <v-text-field v-model="list.staff_property_group_description"
                      :readonly='readonly'
                      append-icon="mdi-longitude"
                      label="Description"></v-text-field> -->
          <mrc_ess_multiText v-model="list.staff_property_group_description"
                             :isNational="judegNational('staff_property_group_description')"
                             :readonly='readonly'
                             label="Description"></mrc_ess_multiText>
        </v-col>
        <v-col cols="12">
          <v-textarea label="Remark"
                      v-model="list.remarks"></v-textarea>
        </v-col>
      </v-row>
    </v-card-text>
    <mrc_ess_submitBtn @success='success'
                       v-if="!readonly"
                       @cancel='cancel'
                       :successText="!$store.state.uuid?$t('label_The_next_step'):$t('label_submit')"></mrc_ess_submitBtn>
  </v-card>
</template> 

<script>
import { required, maxLength, between, email, url } from 'vuelidate/lib/validators'
import { getStaffPropertyGroupDetail } from '@/service/api/staffmanagement.js'
export default {
  components: {},
  props: {
    readonly: {
      type: Boolean
    }
  },
  validations: {
    list: {
      staff_property_group_name: {
        required
      },
      staff_property_group_description: {

      },
      staff_property_group_code: {

      },
      remarks: {

      },
    }
  },
  data () {
    return {
      list: {
        staff_property_group_name: '',    //属性分组名称
        staff_property_group_description: '',    //属性分组描述
        staff_property_group_code: '',    //属性分组编码
        is_default: '',    //是否默认
        org_level_uuid: '',    //所属公司
        remarks: '',    //备注
      }
    };
  },
  created () { },
  async mounted () {
    await this.national('getStaffPropertyGroupDetail')
    if (this.$store.state.uuid) {
      await this.getdetail()
    }
  },
  methods: {
    async getdetail () { //获取整个列表
      const uuid = this.$store.state.uuid
      const res = await getStaffPropertyGroupDetail({
        uuid: this.$store.state.uuid
      })
      this.list = res.data
      console.log(this.list);
    },
    success () {   //详情修改提交按钮  
      if (this.$store.state.uuid) {

      }
      else {
        // this.$v.list.$touch()
        // if (this.$v.list.$error) return
        const org_level_uuid = this.$store.state.currentCompany
        this.list.org_level_uuid = org_level_uuid
        this.$emit('next', 2, this.list)
      }
    },
    cancel () {
      this.$router.push({
        name: 'staffPropertyGroup'
      })
    },
    // nextstep () { //下一步  并获取用户输入数据
    //   // this.$v.list.$touch()
    //   // if (this.$v.list.$error) return
    //   const org_level_uuid = this.$store.state.currentCompany
    //   this.list.org_level_uuid = org_level_uuid
    //   this.$emit('next', 2, this.list)
    // }
  },
  computed: {},
  watch: {
  },
};
</script>

