//员工管理
import http from '../httpConfig'

const table = "mrbase_sys_staff_property_group"


// 获取Staff Property Group列表接口
export const getStaffPropertyGroup = (params = {}) => {
  params['~table~'] = table
  return new Promise((resolve, reject) => {
    http.post('/api/staff-property/list-group/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

